.app-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: var(--tokyo-bg);
  overflow: hidden;
}

/* Header Bar */
.header-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--tokyo-bg-dark);
  border-bottom: 1px solid var(--tokyo-fg-gutter);
  padding: 12px 16px;
  height: auto;
  min-height: 56px;
  flex-shrink: 0;
}

.header-left,
.header-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.header-center {
  flex: 1;
  text-align: center;
}

.header-title {
  color: var(--tokyo-fg-dark);
  font-size: 14px;
  font-weight: 500;
  opacity: 0.8;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 12px;
  height: 100%;
}

.logo-image {
  height: 40px;
  width: auto;
  object-fit: contain;
}

.logo-text {
  font-size: 24px;
  font-weight: 700;
  background: linear-gradient(135deg, var(--tokyo-orange) 0%, var(--tokyo-yellow) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.5px;
  line-height: 1;
}

/* Header Menu */
.header-menu {
  display: flex;
  align-items: center;
  gap: 2px;
  margin-left: 16px;
}

.menu-item {
  padding: 2px 8px;
  background: transparent;
  border: 1px solid var(--tokyo-blue);
  color: var(--tokyo-fg-dark);
  font-size: 12px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 3px;
  white-space: nowrap;
  box-shadow: 0 0 8px rgba(122, 162, 247, 0.4);
  height: 26px;
  display: flex;
  align-items: center;
}

.menu-item:hover {
  background-color: var(--tokyo-bg-highlight);
  color: var(--tokyo-fg);
  border-color: var(--tokyo-orange);
  box-shadow: 0 0 20px rgba(255, 158, 100, 0.8), 0 0 40px rgba(255, 158, 100, 0.4);
}

/* Header buttons */
.header-button {
  padding: 4px 12px;
  background: transparent;
  border: 1px solid var(--tokyo-purple);
  color: var(--tokyo-fg-dark);
  font-size: 12px;
  font-family: 'JetBrains Mono', monospace;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 4px;
  white-space: nowrap;
  box-shadow: 0 0 8px rgba(187, 154, 247, 0.5);
}

.header-button:hover {
  background-color: var(--tokyo-bg-highlight);
  color: var(--tokyo-fg);
  border-color: var(--tokyo-orange);
  box-shadow: 0 0 12px rgba(255, 158, 100, 0.6);
}

/* Documentation button */
.docs-button {
  margin-left: 8px;
  border-color: #ff1493;
  box-shadow: 0 0 8px rgba(255, 20, 147, 0.5);
}

.docs-button:hover {
  border-color: #ff69b4;
  box-shadow: 0 0 16px rgba(255, 105, 180, 0.8);
  color: #ff69b4;
}

/* Status Bar */
.status-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--tokyo-bg-dark);
  border-top: 1px solid var(--tokyo-fg-gutter);
  padding: 10px 12px;
  height: 48px;
  flex-shrink: 0;
  gap: 10px;
  font-size: 12px;
}

.status-left,
.status-right {
  display: flex;
  gap: 8px;
  align-items: center;
}

.status-center {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.status-checkpoint-buttons {
  display: inline-flex;
  gap: 8px;
  align-items: center;
  margin-left: 12px;
}

.status-checkpoint-buttons .status-button {
  padding: 5px 12px;
  font-size: 12px;
  min-width: auto;
  height: 26px;
  background: transparent;
  border: 1px solid var(--tokyo-cyan);
  box-shadow: 0 0 10px rgba(122, 162, 247, 0.6);
  border-radius: 4px;
  color: var(--tokyo-fg);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  font-weight: 500;
  transition: all 0.3s ease;
}

.status-checkpoint-buttons .status-button:hover {
  background: var(--tokyo-bg-highlight);
  border-color: var(--tokyo-orange);
  box-shadow: 0 0 20px rgba(255, 158, 100, 0.8);
  transform: translateY(-1px);
  color: var(--tokyo-orange);
}

.status-text {
  color: var(--tokyo-fg-dark);
  font-size: 11px;
  opacity: 0.8;
}

.status-button {
  padding: 2px 8px;
  background: transparent;
  border: 1px solid var(--tokyo-blue);
  border-radius: 4px;
  color: var(--tokyo-fg-dark);
  font-size: 11px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'JetBrains Mono', monospace;
  box-shadow: 0 0 8px rgba(122, 162, 247, 0.5);
}

.status-button:hover {
  background: var(--tokyo-bg-highlight);
  color: var(--tokyo-fg);
  border-color: var(--tokyo-orange);
  box-shadow: 0 0 12px rgba(255, 158, 100, 0.6);
}

/* Explorer content */
.explorer-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #24283b;
  border-right: 1px solid var(--tokyo-fg-gutter);
}

/* File explorer wrapper - 75% of parent height */
.file-explorer-wrapper {
  height: 75%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

/* Future component placeholder - 25% of parent height */
.explorer-future-component {
  height: 25%;
  border-top: 1px solid var(--tokyo-fg-gutter);
  background-color: var(--tokyo-bg-dark);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.placeholder-content {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 16px;
}

.placeholder-text {
  color: var(--tokyo-fg-dark);
  font-size: 12px;
  font-style: italic;
  opacity: 0.6;
  text-align: center;
}

.explorer-actions {
  padding: 8px;
  border-top: 1px solid var(--tokyo-fg-gutter);
}

/* Action buttons */
.action-button {
  width: 100%;
  padding: 6px 12px;
  background: var(--tokyo-bg-highlight);
  border: 1px solid var(--tokyo-blue);
  border-radius: 4px;
  color: var(--tokyo-fg);
  font-size: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'JetBrains Mono', monospace;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  box-shadow: 0 0 10px rgba(122, 162, 247, 0.5);
}

.action-button:hover {
  background: var(--tokyo-bg-dark);
  border-color: var(--tokyo-orange);
  color: var(--tokyo-orange);
  box-shadow: 0 0 15px rgba(255, 158, 100, 0.6);
}

/* Editor placeholder */
.editor-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: var(--tokyo-fg-dark);
}

.editor-placeholder h2 {
  font-size: 18px;
  margin-bottom: 8px;
  color: var(--tokyo-fg);
}

.editor-placeholder p {
  font-size: 14px;
  opacity: 0.6;
}

/* Right panel content */
.right-panel-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 12px;
  gap: 12px;
  overflow-y: auto;
}

.react-bits-button {
  width: 100%;
  padding: 8px 16px;
  background: var(--tokyo-bg-highlight);
  border: 1px solid var(--tokyo-blue);
  border-radius: 6px;
  color: var(--tokyo-fg);
  font-size: 13px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'JetBrains Mono', monospace;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  box-shadow: 0 0 10px rgba(122, 162, 247, 0.5);
}

.react-bits-button:hover {
  background: var(--tokyo-bg-dark);
  border-color: var(--tokyo-orange);
  color: var(--tokyo-orange);
  box-shadow: 0 0 15px rgba(255, 158, 100, 0.6);
}

.preview-section {
  flex: 1;
  overflow: auto;
}

/* Editor with tabs layout */
.editor-with-tabs {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: var(--tokyo-bg);
  flex: 1;
}

.editor-with-tabs .code-editor {
  flex: 1;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .header-menu {
    gap: 2px;
  }
  
  .menu-item {
    padding: 4px 8px;
    font-size: 12px;
  }
}

@media (max-width: 768px) {
  .header-menu {
    display: none;
  }
  
  .header-title {
    display: none;
  }
}

/* Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(2px);
}

.modal-content {
  background-color: var(--tokyo-bg-dark);
  border: 1px solid var(--tokyo-fg-gutter);
  border-radius: 8px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  max-width: 90vw;
  max-height: 90vh;
  overflow: auto;
  position: relative;
}

.modal-close {
  position: absolute;
  top: 12px;
  right: 12px;
  background: var(--tokyo-bg-highlight);
  border: 1px solid var(--tokyo-fg-gutter);
  border-radius: 4px;
  color: var(--tokyo-fg);
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 18px;
  line-height: 1;
  z-index: 1001;
  transition: all 0.2s ease;
}

.modal-close:hover {
  background: var(--tokyo-red);
  color: white;
  border-color: var(--tokyo-red);
}

