.menu-bar {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
}

.menu-container {
  position: relative;
}

.menu-button {
  background: none;
  border: none;
  color: #cccccc;
  padding: 4px 12px;
  cursor: pointer;
  font-size: 13px;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.menu-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.menu-button.active {
  background-color: rgba(255, 255, 255, 0.15);
}

.menu-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 200px;
  background: #252526;
  border: 1px solid #454545;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  padding: 4px 0;
  z-index: 1000;
  margin-top: 2px;
}

.menu-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 6px 20px;
  cursor: pointer;
  font-size: 13px;
  color: #cccccc;
  transition: background-color 0.2s;
}

.menu-item:hover {
  background-color: #094771;
}

.menu-label {
  flex: 1;
}

.menu-shortcut {
  color: #999999;
  font-size: 11px;
  margin-left: 20px;
}

.menu-separator {
  height: 1px;
  background-color: #454545;
  margin: 4px 0;
}