/**
 * SessionSummaryButton Styles
 * Glassmorphism Theme to match PRD Generator
 */

/* Session Summary Button - PRD Generator Style */
.session-summary-button {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 13px;
  font-weight: 500;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  transition: all 0.3s ease;
  margin-left: 8px;
  white-space: nowrap;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  gap: 6px;
}

.session-summary-button:hover:not(.disabled) {
  background: rgba(139, 92, 246, 0.3);
  border-color: rgba(139, 92, 246, 0.5);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(139, 92, 246, 0.2);
  color: rgba(255, 255, 255, 1);
}

.session-summary-button:active:not(.disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 15px rgba(139, 92, 246, 0.2);
}

.session-summary-button.disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* Modal Overlay */
.session-summary-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* Modal Container */
.session-summary-modal {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 16px;
  max-width: 90vw;
  max-height: 85vh;
  width: 900px;
  position: relative;
  backdrop-filter: blur(20px);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.4), 0 8px 25px rgba(139, 92, 246, 0.2);
  overflow: hidden;
  animation: slideIn 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}

@keyframes slideIn {
  from { 
    opacity: 0; 
    transform: scale(0.9) translateY(40px); 
  }
  to { 
    opacity: 1; 
    transform: scale(1) translateY(0); 
  }
}

/* Modal Close Button */
.modal-close-x {
  position: absolute;
  top: 16px;
  right: 16px;
  background: rgba(239, 68, 68, 0.15);
  border: 1px solid rgba(239, 68, 68, 0.25);
  color: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  width: 36px;
  height: 36px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10001;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.modal-close-x:hover {
  background: rgba(239, 68, 68, 0.25);
  border-color: rgba(239, 68, 68, 0.4);
  transform: scale(1.1);
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.2);
}

/* Loading State */
.summary-modal-loading {
  padding: 80px 40px;
  text-align: center;
  color: rgba(255, 255, 255, 0.9);
}

.summary-modal-loading h3 {
  color: rgba(139, 92, 246, 0.9);
  margin-bottom: 20px;
  font-size: 22px;
  font-weight: 600;
}

.summary-modal-loading p {
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 30px;
  line-height: 1.5;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 3px solid rgba(139, 92, 246, 0.2);
  border-top: 3px solid rgba(139, 92, 246, 0.8);
  border-radius: 50%;
  animation: spin 1.2s linear infinite;
  margin: 0 auto 30px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-progress {
  text-align: left;
  max-width: 300px;
  margin: 0 auto;
}

.progress-item {
  padding: 8px 0;
  color: var(--tokyo-green);
  opacity: 0;
  animation: fadeInProgress 0.5s ease forwards;
}

.progress-item:nth-child(1) { animation-delay: 0.2s; }
.progress-item:nth-child(2) { animation-delay: 0.8s; }
.progress-item:nth-child(3) { animation-delay: 1.4s; }
.progress-item:nth-child(4) { animation-delay: 2.0s; }

@keyframes fadeInProgress {
  to {
    opacity: 1;
  }
}

/* Error State */
.summary-modal-error {
  padding: 60px 40px;
  text-align: center;
  color: rgba(255, 255, 255, 0.9);
}

.summary-modal-error h3 {
  color: rgba(239, 68, 68, 0.9);
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 600;
}

.error-message {
  color: rgba(255, 182, 193, 0.9);
  margin-bottom: 30px;
  background: rgba(239, 68, 68, 0.1);
  padding: 20px;
  border-radius: 12px;
  border: 1px solid rgba(239, 68, 68, 0.25);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  gap: 12px;
}

.error-actions {
  display: flex;
  gap: 16px;
  justify-content: center;
}

/* Success Content */
.summary-modal-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-height: 85vh;
}

.summary-header {
  padding: 24px 28px 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.1), rgba(59, 130, 246, 0.05));
  position: relative;
}

.summary-header h3 {
  color: rgba(255, 255, 255, 0.95);
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 12px;
}

.warning-badge {
  background: rgba(245, 158, 11, 0.15);
  color: rgba(251, 191, 36, 0.9);
  padding: 6px 12px;
  border-radius: 8px;
  font-size: 12px;
  font-weight: 500;
  border: 1px solid rgba(245, 158, 11, 0.25);
  backdrop-filter: blur(10px);
}

/* Summary Text Container */
.summary-text {
  flex: 1;
  overflow-y: auto;
  padding: 28px;
  background: rgba(255, 255, 255, 0.03);
}

.summary-text pre {
  font-family: 'Monaco', 'Menlo', 'Consolas', 'JetBrains Mono', monospace;
  font-size: 14px;
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.9);
  white-space: pre-wrap;
  word-wrap: break-word;
  margin: 0;
  background: transparent;
}

/* Actions Bar */
.summary-actions {
  padding: 24px 28px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  gap: 16px;
  justify-content: flex-end;
  background: rgba(255, 255, 255, 0.03);
}

/* Action Buttons */
.copy-button, .regenerate-button, .close-button, .retry-button {
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 1px solid transparent;
  font-family: 'Inter', sans-serif;
  display: flex;
  align-items: center;
  gap: 8px;
  backdrop-filter: blur(10px);
}

/* Subtle button styles matching PRD generator */
.copy-button {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.9);
  border-color: rgba(255, 255, 255, 0.1);
  box-shadow: none;
}

.copy-button:hover:not(:disabled) {
  background: rgba(147, 51, 234, 0.1);
  border-color: rgba(147, 51, 234, 0.3);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(147, 51, 234, 0.15);
}

.copy-button.success {
  background: rgba(34, 197, 94, 0.15);
  border-color: rgba(34, 197, 94, 0.3);
  cursor: default;
}

.regenerate-button, .retry-button {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.9);
  border-color: rgba(255, 255, 255, 0.1);
  box-shadow: none;
}

.regenerate-button:hover:not(:disabled),
.retry-button:hover:not(:disabled) {
  background: rgba(147, 51, 234, 0.1);
  border-color: rgba(147, 51, 234, 0.3);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(147, 51, 234, 0.15);
}

.close-button {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.7);
  border-color: rgba(255, 255, 255, 0.1);
  box-shadow: none;
}

.close-button:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(255, 255, 255, 0.1);
}

/* Disabled state for buttons in session summary modal only */
.session-summary-modal button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.session-summary-modal button:disabled:hover {
  transform: none !important;
  box-shadow: none !important;
}

/* Scrollbar Styling */
.summary-text::-webkit-scrollbar {
  width: 8px;
}

.summary-text::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
}

.summary-text::-webkit-scrollbar-thumb {
  background: rgba(139, 92, 246, 0.3);
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.summary-text::-webkit-scrollbar-thumb:hover {
  background: rgba(139, 92, 246, 0.5);
}

/* Responsive Design */
@media (max-width: 768px) {
  .session-summary-modal {
    width: 95vw;
    height: 95vh;
    max-width: none;
    max-height: none;
  }
  
  .summary-actions {
    flex-wrap: wrap;
  }
  
  .session-summary-button {
    font-size: 11px;
    padding: 3px 6px;
  }
}